/*
 * SETTINGS-COLORS
 */

/*
  * Primary colors
  */

.header {
  text-align: center;
  font-size: .8em;
  padding-bottom: 1px;
  background-color: peachpuff;
}

/*
  Exports the variable to be exposed in CSS Modules (styles object in React)
*/

:export {
  header_bg: peachpuff;
}







